import React from 'react'
import { Link } from 'gatsby'
import styled from '@emotion/styled'
import Headroom from 'react-headroom'
import logo_default from '../../static/logo/logo_default.svg'

const Nav = styled.nav`
    position: relative;
    font-weight: 400;
    font-size: 1.2rem;
    @media (max-width: 400px) {
        font-size: 0.8rem;
    }
    transform: translateY(100%);
    height: 50px;
    background: transparent;
    display: flex;
    float: right;
    a {
        position: relative;
        font-family: "Domine";
        color: #ac8d64;
        transition: all 0.25s ease-out
        @media (max-width: 500px) {
            padding-left: 0.7rem;
            padding-right: 0.7rem;
        }
        @media (max-width: 400px) {
            padding-left: 0.6rem;
            padding-right: 0.6rem;
        }
        padding-left: 1rem;
        padding-right: 1rem;
        overflow: hidden;
        div {
            position: relative;
            height: 1px;
            width: 0%;
            top: 0px;
            left: 50%;
            background-color: #dc2830;
            transition: all 0.45s ease-out;
        }
        &:hover>div {
            width: 100%;
            left: 0%;
            background-color: #dc2830;
            transition: all 0.45s ease-out;
        }
    }
`
const Logo = styled.img`
    position: relative;
    width: 148px;
    top: 0px;
`

const StyledLink = styled(Link)`
    position: fixed;
    display: inline-block;
`

const Pinned = function() {
};

const unPinned = function() {
};

const unFixed = function() {
};

const NavBar = () => (
    <Headroom id="main_headroom" disableInlineStyles
    onPin={Pinned} onUnpin={unPinned} onUnfix={unFixed} 
    fixAnchor={0} pinAnchor={0} hideAnchor={150} >
        <StyledLink id="main-header-logo" to="/">
            <Logo id="logo_default" src={logo_default} alt='Dumna Niepodległa Logo' />
        </StyledLink>
        <Nav id="nav">  
            <Link to='/'>
                Wydarzenia
                <div />
            </Link>
            <Link to='/'>
                O nas
                <div />
            </Link>
            <Link to='/'>
                Kontakt
                <div />
            </Link>
            <Link to='/'>
                Sklep
                <div />
            </Link>
        </Nav>
    </Headroom>
);

export default NavBar